import { calculateCurrentGoodId } from "../../../utils/reducers-utils/products/products-utils";
import { setCurrentPrice } from "./price-reducer";




let initialState = {
    //       
    prof: {
        internet: [],
        abon: [],
        abonbig: [],
        proxima: []
    },
    universal: {
        internet: [],
        proxima: []
    },

    
    
    currentId: 6394

}


//A
let SET_CURRENT_PRODUCT = 'SET_CURRENT_PRODUCT ';
let SET_PRODUCTS = 'SET_PRODUCTS';


//AC
const setCurrentProduct = (complectIndex, odIndex, typeOfContract, currentSupply, currentComplectsType) => {

    return {
        type: SET_CURRENT_PRODUCT,
        complectIndex,
        odIndex,
        typeOfContract,
        currentSupply,
        currentComplectsType,
        
    }
}
export const setProducts = (products) => ({type: SET_PRODUCTS, products})



//THUNK

export const changeCurrentProductAndPrice = () => (dispatch, getState) => {
    const state = getState()
    let currentContract = state.contract.current
    let currentComplect = state.currentComplect


    let currentSupply = state.global.currentSupply
    let currentComplectsType = state.global.currentComplectsType
    let currentRegion = state.global.currentRegion
    let odIndex = state.od.currentOdIndex
    let complectIndex = null
    let typeOfContract = null

    if (currentContract && currentComplect) {
        complectIndex = currentComplect.number
        typeOfContract = currentContract.value

        dispatch(setCurrentProduct(complectIndex, odIndex, typeOfContract, currentSupply.title, currentComplectsType.title))
        dispatch(setCurrentPrice(complectIndex, odIndex, currentContract, currentComplectsType.title, currentRegion))
    } else {

        console.log('something wrong with current current good and current price')
    }


}



//REDUCER
export const products = (state = initialState, action) => {

    switch (action.type) {


        case SET_CURRENT_PRODUCT:
           

            let currentId = calculateCurrentGoodId(
                state.prof.internet,
                state.prof.abon,
                state.prof.abonbig,
                state.prof.proxima,

                state.universal.internet,
                state.universal.proxima,

                action.complectIndex,
                action.odIndex,

                action.typeOfContract,
                action.currentSupply,
                action.currentComplectsType
                )

            return state.currentId !== currentId
                ? { ...state, currentId }
                : state

        case SET_PRODUCTS:
            return { ...state, ...action.products }         
        default:
            return state
    }

}