
const FieldInfoblocks = ({ group }) => {
    return (
        <>
            <p className="field-group">{group.groupsName}</p>
            {group.value.map(
                (field) => field.checked && <p key={`deal-field-${group.groupsName}-${field.name}`} className="field-text">{field.title || field.name}</p>
            )}
        </>
    );
};

export default FieldInfoblocks