import React from 'react';
import { FormControl, InputLabel, MenuItem, Select } from "@mui/material"
import './Selects.css';



const Selects = ({ name, items, currentValue, onSelectClick }) => {

    let ref = React.createRef()

    return (
        <div className="contract__container">
            <FormControl fullWidth variant="outlined" >
                <InputLabel id="deal-contract-select-label">{name}</InputLabel>
                <Select
                    style={{ height: '54px', padding: 3, border: 'none', minWidth: '100%' }}
                    // labelId={`${name}-select-label`}
                    id={`${name}-select`}
                    value={currentValue}
                    label={currentValue}
                    ref={ref}
                >
                    {items.map((item, index) => {

                        return (<MenuItem key={`${index}-${item.id || item}`} onClick={(e) => {
                            e.preventDefault()
                            
                            onSelectClick(
                                index
                            )
                        }} className="btn" value={item.name || item.value || item}>{item.name || item.value || item}</MenuItem>)
                    })}
                </Select>
            </FormControl>
        </div>
    )
}

export default Selects

