import { CONTRACT_ELEVEN, CONTRACT_SIX } from "../../../redux/reducers/deal/contract-reducer"
import { INTERNET, PROF, PROXIMA, UNIVERSAL } from "../../../redux/reducers/global/global-reducer"

export const calculateCurrentGoodId = (
    profInternet,
    abonSix,
    abonEleven,
    profProxima,

    universalInternet,
    universalProxima,

    complectIndex,
    odIndex,
    typeOfContract,
    currentSupply,
    currentComplectsType) => {

    let currentId
    if (currentSupply === INTERNET) {
        if (currentComplectsType === PROF) {

            if (typeOfContract === CONTRACT_SIX) {
                currentId = abonSix[odIndex][complectIndex]
            } else if (typeOfContract === CONTRACT_ELEVEN) {
                currentId = abonEleven[odIndex][complectIndex]
            } else {

                currentId = profInternet[odIndex][complectIndex]

            }

        } else if (currentComplectsType === UNIVERSAL) {
            currentId = universalInternet[odIndex][complectIndex]
        }

    } else if (currentSupply === PROXIMA) {
        if (currentComplectsType === PROF) {
            currentId = profProxima[odIndex][complectIndex]
        } else if (currentComplectsType === UNIVERSAL) {
            currentId = universalProxima[odIndex][complectIndex]
        }

    }


    return currentId
}