import { getCurrentPrice, getCurrentProductId, getServicesPriceAndProduct } from "../../../utils/reducers-utils/checkboxes/consalting-utils";
import { uncheckBlocks } from "../../../utils/reducers-utils/checkboxes/infoblocks-utils";
import { SET_CONTRACT_IN_BLOCKS } from "../deal/contract-reducer";
import { SET_GLOBAL } from "../global/global-reducer";
import { RESET, RESET_UNIVERSAL } from "../reset/reset-reducer";


let initialState =

{
    groupsName: ' ',
    current: null,
    display: 'none',
    packages: {
        // 0: {
        //     id:0,
        //     description: "",
        //     name: "experts",
        //     price: 0,
        //     productId: "",
        //     rabs: 1,
        //     title: " . , , "
        // }
    },
    value: [
        {
            'name': ' ',
            'checked': false,
            'weight': 0,
            'description': ''
        },
        {
            'name': ' . , , ',
            'checked': false,
            'weight': 0,
            'description': ''
        },
        {
            'name': ' . ',
            'checked': false,
            'weight': 0,
            'description': ''
        },


    ]

}




//A
const SET_CONSALTING = 'SET_CONSALTING'
const SET_FETCHED_CONSALTING = 'SET_FETCHED_CONSALTING'

//AC

export const setFetchedConsalting = (consalting) => ({ type: SET_FETCHED_CONSALTING, consalting })
export const setConsalting = (currentComplect, currentContract) => ({ type: SET_CONSALTING, currentComplect, currentContract })



//REDUCER

const consalting = (state = initialState, action) => {
    switch (action.type) {

        case SET_FETCHED_CONSALTING:

            let value = []
            let packages = action.consalting
            for (const cons in action.consalting) {

                value[cons] = {
                    name: action.consalting[cons].title,
                    checked: false,
                    weight: 0,
                    rabs: action.consalting[cons].rabs,
                    price: 0,
                    description: action.consalting[cons].description,
                    productId: action.consalting[cons].productId
                }
            }

            return { ...state, value, packages }


        case SET_GLOBAL:
            //action.packages : { consalting[id] : { id, name, title, description, productId, productSix, productEleven, rabs, price }}
            let resultPackages = {}

            for (const key in state.packages) {
                let price = Number(state.packages[key].rabs) * Number(action.currentRegion.rabs)
                price = Number(price.toFixed(2))
                resultPackages[key] = { ...state.packages[key], price, currentProduct: state.packages[key].productId }
            }

            return {
                ...state, packages: resultPackages
            }

        case SET_CONSALTING:
            //action: currentComplect
            //currentContract : { id, value, measureIndex, prepayment, discount }
            const result = { ...state }
            result.value = state.value.map((consaltingBlock, index) => {

                if (action.currentComplect.consalting.includes(index) || action.currentComplect.consaltingProduct.includes(index)) {

                    return !consaltingBlock.checked ? { ...consaltingBlock, checked: !consaltingBlock.checked } : consaltingBlock
                } else {

                    return consaltingBlock.checked ? { ...consaltingBlock, checked: !consaltingBlock.checked } : consaltingBlock
                }
            })

            if (action.currentComplect.consaltingProduct.length > 0) {//      -
                result.current = { ...result.packages[action.currentComplect.consaltingProduct[0]] } //       
                result.current = getServicesPriceAndProduct(result.current, action.currentContract)


            } else {
                result.current = null // ,      
            }
            result.current ? result.display = 'flex' : result.display = 'none'  //         

            return result


        case SET_CONTRACT_IN_BLOCKS:
            //action: currentContract : { id, value, measureIndex, prepayment, discount }

            if (state.current) {

                let resultPackage = { ...state.current }
                let searchingIndex = resultPackage.id
                resultPackage = getServicesPriceAndProduct(state.packages[searchingIndex], action.currentContract)

                return { ...state, current: resultPackage }
            } else {
                return state
            }

        case RESET || RESET_UNIVERSAL:

            return { ...uncheckBlocks([state])[0], current: null, display: 'none' }


        default:
            return state
    }


}

export default consalting 