import { connect } from "react-redux"
import DealPriceTable from "./Deal-Price-Table"



const mapStateToProps = (state) => {


    return {
        currentComplect: state.currentComplect,
        price: state.price.current.price,
        lt: state.legalTech,
        consalting: state.consalting,
        unit: state.contract.current.measure.value
    }
}


export const DealPriceTableContainer = connect(mapStateToProps, {})(DealPriceTable)

