import { connect } from "react-redux";
import ResultButton from "./Result-Button";
import { setDealTableStatus } from "../../../../redux/reducers/deal/deal-reducer";


const mapStateToProps = (state) => {



    return {
        currentComplect: state.currentComplect,
        resultError: state.result.resultItems[1].value,
        allComplects: state.allComplects,
        currentComplectsType: state.global.currentComplectsType && state.global.currentComplectsType.title,
        isDealTable: state.deal.table.isActive
    }
}
const mapDispatchToProps = (dispatch) => {

    return {
        handleClose: (element) => {
            // let actionClose = resultModalActionCreator(false, element.current)
            // return dispatch(actionClose)
            return dispatch(setDealTableStatus(false))
        },
        handleShow: (element) => {
            // let actionShow = resultModalActionCreator(true, element.current)
            // return dispatch(actionShow)
            return dispatch(setDealTableStatus(true))
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ResultButton)