import { applyMiddleware, combineReducers, createStore } from 'redux';
import ThunkMiddleware from 'redux-thunk'
import global from './reducers/global/global-reducer';
import app from './reducers/app/app-reducer';
import { contract } from './reducers/deal/contract-reducer';
import preloader from './reducers/preloader/preloader-reducer';
import { theme } from './reducers/style/theme-reducer';
import { currentComplect } from './reducers/complect/current-complect-reducer';
import profs from './reducers/complect/prof-complects-reducer';
import universals from './reducers/complect/universal-complects-reducer';
import { od } from './reducers/od/od-reducer';
import infoblocks from './reducers/checkboxes/infoblocks-reducer';
import encyclopedias from './reducers/checkboxes/encyclopedias-reducer';
import consalting from './reducers/checkboxes/consalting-reducer';
import legalTech from './reducers/checkboxes/legal-tech-reducer';
import freeBlocks from './reducers/checkboxes/freeblocks-reducer';
import result from './reducers/result/result-reducer';
import price from './reducers/deal/price-reducer';
import { products } from './reducers/deal/products-reducer';
import { dealName } from './reducers/deal/deal-name-reducer';
import deal from './reducers/deal/deal-reducer';
import { field } from './reducers/deal/field/field-reducer';
import { bitrixFields } from './reducers/deal/bitrix-fields/bitrix-fields-reducer';



let reducers = combineReducers({
    app,
    global,
    // theme,
    preloader,

    currentComplect,
    profs,
    universals,
    od,

    infoblocks,
    encyclopedias,
    consalting,
    legalTech,
    freeBlocks,
    result,
    price,

    deal,
    products,
    contract,
    dealName,
    field,
    bitrix:bitrixFields

});

const store = createStore(reducers, applyMiddleware(ThunkMiddleware))

export default store