export const getCurrentProductId = (currentMeasureIndex, consaltingPackage) => {

    let currentProductId = { ...consaltingPackage }

    if (currentMeasureIndex === 0) { //
        currentProductId = consaltingPackage.productId
    } else if (currentMeasureIndex === 1) { //. 6 .
        currentProductId = consaltingPackage.productSix
    } else if (currentMeasureIndex === 2) { //. 12 .
        currentProductId = consaltingPackage.productEleven
    }

    return currentProductId
}

export const getCurrentPrice = (price, prepayment = 1) => {

    let resultPrice = Number(price) * Number(prepayment)
    resultPrice = Number(resultPrice.toFixed(2))

    return resultPrice
}

export const getServicesPriceAndProduct = (currentService, currentContract) => {
    let resultCurrentService = {...currentService}
    if (currentContract) {
        resultCurrentService.price = getCurrentPrice(resultCurrentService.price, currentContract.prepayment)
        resultCurrentService.currentProduct = getCurrentProductId(currentContract.measureIndex, resultCurrentService)

    } else {
        resultCurrentService.currentProduct = resultCurrentService.productId
        resultCurrentService.price = getCurrentPrice(resultCurrentService.price)
    }
    return resultCurrentService
}