

/////////FILLING INFOBLOCKS


export const fillingInfoblocks = (currentComplect, checked, value) => {

    //    currentComplect
    let resultState = currentComplect
    if (currentComplect) {
        resultState = { ...currentComplect, filling: [...currentComplect.filling] }

        if (currentComplect.isChanging) {
            if (checked) {   //   checked  false
                resultState.filling.forEach((elem, idx) => {
                    if (elem === value) {
                        if (resultState.name === ' PRO' && value === ' ') {

                        } else if (value === ' -   .  ' ||
                            value === ' .  '
                        ) {

                        } else if (value === ' -   ') {

                            resultState.filling = resultState.filling.filter(infoblock => (infoblock !== ' -   ' && infoblock !== ' -   .  '))
                        } else if (value === ' ') {

                            resultState.filling = resultState.filling.filter(infoblock => (infoblock !== ' ' && infoblock !== ' .  '))
                        } else {
                            resultState.filling.splice(idx, 1)
                        }
                    }
                })

                //         


                if (value === '    ' || value === '   ') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 8)
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 9)
                }
                if (value === '   ') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 6)
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 7)
                }
                if (value === '   ') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 6)
                }



            } else {
                if (value === ' -   .  ' ||
                    value === ' .  '
                ) {

                } else if (value === ' -   ') {
                    resultState.filling.push(value)
                    resultState.filling.push(' -   .  ')
                } else if (value === ' ') {

                    resultState.filling.push(value)
                    resultState.filling.push(' .  ')
                } else {
                    resultState.filling.push(value)
                }

                if (value === '    ') {
                    if (!resultState.freeBlocks.includes(8) || !resultState.freeBlocks.includes(9)) {
                        resultState.freeBlocks.push(8)
                        resultState.freeBlocks.push(9)
                    }


                }
                if (value === '   ') {
                    if (!resultState.freeBlocks.includes(6) || !resultState.freeBlocks.includes(7)) {
                        resultState.freeBlocks.push(6)
                        resultState.freeBlocks.push(7)
                    }
                }
                if (value === '   ') {
                    if (!resultState.freeBlocks.includes(6)) {
                        resultState.freeBlocks.push(6)

                    }
                }

            }
        }
    }

    return resultState
}


/////////FILLING ENCYCLOPEDIAS
export const fillingEr = (currentComplect, packets, checked, index) => { //         checked  uncheck  -   state    filling 

    //ersInPacket

    let ers = [...currentComplect.ers]
    let packetsInComplect = [...currentComplect.packetsEr]
    let ersInPacketsInComplect = [...currentComplect.ersInPacket]

    let activePackets = packets.filter((pac, i) => packetsInComplect.includes(i))



    if (checked === true) {  //   
        // if (ers.length > -1) {

        ers = ers.filter(er => er !== index)
        if (currentComplect.name !== '' && ersInPacketsInComplect.includes(index)) { //          
            //         -  ,      -   
            packets.forEach((packet, packetIndex) => { //  ( ,   encyclopedia-reducer)

                if (packet.including.includes(index) && activePackets.some(activePacket => activePacket.name === packet.name)) {           //              
                    packetsInComplect = packetsInComplect.filter(packetInComplect => packetInComplect !== packetIndex) //     currentComplect
                    ersInPacketsInComplect = ersInPacketsInComplect.filter(erInPacket => !packet.including.includes(erInPacket)) //      currentComplect.ersInPacket
                    const newPacketsInComplect = packet.including.filter(erIndex => erIndex !== index)  //         ,    ,      currentComplect.ers

                    ers = [...new Set(ers.concat(newPacketsInComplect))];
                    //  arr1  arr2    concat.     ,  -   Set,   . 
                    console.log(ers)



                }

            })


        } else if (currentComplect.name === '' && !ersInPacketsInComplect.includes(index) || (currentComplect.name != '' && !ersInPacketsInComplect.includes(index))) { // /       
            ers = ers.filter(er => er !== index)
        }
    } else {

        if (!ers.includes(index)) ers.push(index)
    }


    return {
        ...currentComplect,
        ers: ers,
        packetsEr: packetsInComplect,
        ersInPacket: ersInPacketsInComplect
    }
}


export const fillingPketsEr = (currentComplect, packets, checked, index) => { // currentComplect
    let packetsEr = [...currentComplect.packetsEr]
    let ers = [...currentComplect.ers]
    let ersInPacketsInComplect = [...currentComplect.ersInPacket]
    const defaultErInPacket = packets[index].including

    const changePaketsInState = (checked, index) => { //             checked  uncheck   allEnciclopedis -   state    filling 

        if (checked === true) {
            packetsEr = [] // -         state.currentComplect
            ersInPacketsInComplect = []
        } else {
            packetsEr = [index]
            ersInPacketsInComplect = [...defaultErInPacket]

        }

        // changePaketsErFromCurrent(state);
    }
    const setErInPacketsEr = (indexesOfActivePackets, packets) => {
        //         (   )
        //  ,          
        //c    
        //          
        let result = []
        packets.forEach((pack, i) => {

            if (indexesOfActivePackets.includes(i)) {

                result = [...new Set(result.concat(pack.including))]
            }
        })
        return result
    }

    if (currentComplect.isChanging) {

        if (currentComplect.name !== '') {
            changePaketsInState(checked, index)
            //      
            //TODO: if universal complect
        } else {

            window.alert('  -      ')
            if (checked === true) {
                if (index === 0) {
                    packetsEr = [1, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 1) {
                    packetsEr = [0, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 2) {
                    packetsEr = [0, 1]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                }

            } else { //checked == false
                if (index === 0) {
                    packetsEr = [0, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 1) {
                    packetsEr = [0, 1]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 2) {
                    packetsEr = [1, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                }
            }
        }
    }
    // 0, 2, 4, 5, 6, 0, 3, 4, 6, 7
    //            :
    ers = ers.filter((item) => !defaultErInPacket.includes(item))
    currentComplect.packetsEr = packetsEr
    currentComplect.ers = ers
    currentComplect.ersInPacket = ersInPacketsInComplect
    return { ...currentComplect }
}


/////////FILLING FREEBLOCKS
export const fillingFreeblocksWithUncheck = (currentComplect, currentFreeblockIndex) => {
    let freeblocks = []

    currentComplect.freeBlocks.forEach(freeblockIndex => {
        if ((currentFreeblockIndex === 9 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 8 && currentFreeblockIndex === freeblockIndex)) {
            //   // 9 - -      
            // 8 - -   
            if (currentComplect.filling.includes('   ') || currentComplect.filling.includes('    ')) { //        
                //    ,    ,    
                freeblocks.push(freeblockIndex)

            }
        } else if ((currentFreeblockIndex === 6 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 7 && currentFreeblockIndex === freeblockIndex)) {
            // 6 - -       
            // 7 - -   
            if (currentComplect.filling.includes('   ')) { //        
                //    ,    ,    
                freeblocks.push(freeblockIndex)

            }
        } else if (currentFreeblockIndex === 6 && currentFreeblockIndex === freeblockIndex) {
            // 6 - -    
            if (currentComplect.filling.includes('   ')) {
                //    ,    ,    
                freeblocks.push(freeblockIndex)
            }


            // 10 -  . :     
            // 11 - 2    
            // 12 -    
        } else if ((currentFreeblockIndex === 10 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 12 && currentFreeblockIndex === freeblockIndex)) {


            if (currentComplect.consaltingProduct.includes(1) || currentComplect.consaltingProduct.includes(2)) {  // 

                freeblocks.push(freeblockIndex)
            }

        } else if ((currentFreeblockIndex === 11 && currentFreeblockIndex === freeblockIndex)) {
            if (currentComplect.consaltingProduct.includes(2)) {  //  


                freeblocks.push(freeblockIndex)
            }


        } else if (currentFreeblockIndex !== freeblockIndex) {
            //  -,     ,       
            freeblocks.push(freeblockIndex)
        }
    })


    return freeblocks
}





/////////FILLING LEGAL TECH

export const fillingLt = (comeCurrentComplect, checked, index) => {
    let currentComplect = { ...comeCurrentComplect }
    currentComplect.lt = [...currentComplect.lt]
    currentComplect.ltInPacket = [...currentComplect.ltInPacket]

    const currentWeightOfIncludedLT = (currentComplect) => {
        let currentWeight = 0
        currentWeight = currentComplect.lt.length

        return currentWeight
    }

    if (currentComplect) {
        if (currentComplect.name === '') {
            // let currentWeight
            let currentWeightOfIncluded
            if (currentComplect.lt.includes(index) === false) { //      -    
                if (checked === false) {

                    currentWeightOfIncluded = currentWeightOfIncludedLT(currentComplect)
                    currentWeightOfIncluded >= 5 ? currentComplect.ltInPacket.push(index) : currentComplect.lt.push(index) // 5      LT  ,   included


                } else {

                    currentComplect.ltInPacket.forEach((elem, idx) => {

                        if (elem === index) {
                            currentComplect.ltInPacket.splice(idx, 1)
                        }


                    })
                }
            } else { //     -    
                if (checked === false) {

                } else {
                    // state.legalTech.value[action.index].checked = false
                    currentComplect.lt.forEach((elem, idx) => {
                        if (elem === index) {
                            currentComplect.lt.splice(idx, 1)
                        }

                    })
                    currentWeightOfIncluded = currentWeightOfIncludedLT(currentComplect)

                    if (currentWeightOfIncluded < 5) { // included <5     -  -     
                        if (currentComplect.ltInPacket.length > 0) {
                            currentComplect.lt.push(currentComplect.ltInPacket[currentComplect.ltInPacket.length - 1]) //      
                            currentComplect.ltInPacket.splice(currentComplect.ltInPacket.length - 1, 1) //    
                        } else {
                            window.alert(`  -    5  Legal Tech,   ${currentWeightOfIncluded}  `)

                        }
                    }
                }
            }

        } else if (currentComplect.name !== ' PRO') {

            if (currentComplect.lt.includes(index) === false) {
                if (checked === false) {
                    currentComplect.ltInPacket.push(index)
                } else {

                    currentComplect.ltInPacket.forEach((elem, idx) => {
                        if (elem === index) {
                            currentComplect.ltInPacket.splice(idx, 1)
                        }
                    })
                }
            }
        } else {
            if (currentComplect.name === ' PRO' && index !== 3) {
                // if (currentComplect.ltInPacket.length === 0) {
                if (checked) {
                    if (currentComplect.ltInPacket.length === 0) {
                        currentComplect.lt = [3]
                    } else {
                        if (currentComplect.lt.includes(index)) {
                            currentComplect.lt = currentComplect.lt.filter(ltIndex => ltIndex != index)
                        }
                        if (currentComplect.ltInPacket.includes(index)) {
                            currentComplect.ltInPacket = currentComplect.ltInPacket.filter(ltIndex => ltIndex != index)
                        }
                    }




                } else {
                    if (currentComplect.lt.length > 1) {
                        currentComplect.ltInPacket.push(index)
                    } else {
                        currentComplect.lt.push(index)
                    }
                }
                // }


            }
        }
    }

    return { ...currentComplect }


}

export const fillingConsalting = (currentComplect, checked, index) => {
    if (index !== 0 && currentComplect && !currentComplect.withConsalting) { //        //       
        let resultCurrentComplect = { ...currentComplect }
        resultCurrentComplect.consalting = [...currentComplect.consalting]
        resultCurrentComplect.consaltingProduct = [...currentComplect.consaltingProduct]

        if (checked) { //  

            resultCurrentComplect.consaltingProduct.includes(index) ?
                resultCurrentComplect.consaltingProduct = []
                : resultCurrentComplect.consaltingProduct = resultCurrentComplect.consaltingProduct


            resultCurrentComplect.freeBlocks = resultCurrentComplect.freeBlocks.filter(blockIndex => blockIndex !== 12 && blockIndex !== 11 && blockIndex !== 10)


        } else { //  

            resultCurrentComplect.consaltingProduct = [index]

            if (index === 1) {
                [10, 12].forEach(blockIndex => !resultCurrentComplect.freeBlocks.includes(blockIndex) && resultCurrentComplect.freeBlocks.push(blockIndex))
                if (resultCurrentComplect.freeBlocks.includes(11)) {
                    resultCurrentComplect.freeBlocks = resultCurrentComplect.freeBlocks.filter(blockIndex => blockIndex !== 11)
                }

            } else if (index === 2) {
                [10, 11, 12].forEach(blockIndex => !resultCurrentComplect.freeBlocks.includes(blockIndex) && resultCurrentComplect.freeBlocks.push(blockIndex))
            }

        }

        debugger
        return resultCurrentComplect
    } else {
        return currentComplect
    }

}