import { Checkbox } from "@mui/material"
import "./Checkbox.scss"



const CheckBox = (props) => {
  return (
    <label
      key={`label-checkbox-${props.groupsName}-${props.index}`}
      className={`infoblock__label  ${props.checked ? ' text--selected' : ' text--base'}`}
      
    >

      <Checkbox
        id={`checkbox-${props.groupsName}-${props.index}`}
        type="checkbox"
        checked={props.checked}
        disabled={false}
        style={{ color: 'black' }}
        value={props.name}
        onClick={(e) => {
          e.stopPropagation()
          
          props.changeCheckbox(
            props.groupsName,
            props.name,
            props.checked,
            props.index)
        }}
        />
      {props.title || props.name}

    </label>)
}

export default CheckBox