import { connect } from "react-redux"
import DealField from "./Deal-Field"


const mapStateToProps = (state) => {
    let fields = [state.infoblocks, [state.freeBlocks], [state.consalting]]
    return {
        currentComplect: state.currentComplect,
        // infoblocks: state.infoblocks,
        encyclopedias: state.encyclopedias,
        // consalting: [state.consalting],
        // legalTech: [state.legalTech],
        lt: state.legalTech,
        fields: fields

    }
}


export default connect(mapStateToProps, {})(DealField)