export const checkUncheck = (infoblocks, currentComplect) => {
    let result = infoblocks
   

        result = infoblocks.map((element) => {
            element.value = element.value.map((elem) => {
                if (currentComplect.filling.includes(elem.name)) {
                    //        ,
                    //     
                    //       
                    //  
                    //          
                    return elem.checked ? elem : { ...elem, checked: true }

                } else {
                    //        ,
                    //     
                    return !elem.checked ? elem : { ...elem, checked: false }
                }
            })
            return element
        })

        return result
}

export const uncheckBlocks = (infoblocks) => {
    
    let result = infoblocks
   
        result = infoblocks.map((group) => {
            group.value = group.value.map((infoblock) => {
                    return !infoblock.checked ? infoblock : { ...infoblock, checked: false }
            })
            return group
        })

        return result
}



