import { setConsalting } from "./consalting-reducer"
import { setERs, setPacketERs } from "./encyclopedias-reducer"
import { setFreeblocks } from "./freeblocks-reducer"
import { setInfoblocks } from "./infoblocks-reducer"
import { setLt } from "./legal-tech-reducer"


export const LT_GROUPS = ['Legal Tech']
export const CONSALTING_GROUPS = [' ']
export const FREE_GROUPS = ['  ']



//THUNK
export const setCheckboxes = (currentComplect) => (dispatch, getState) => {

    let currentContract = getState().contract.current
    dispatch(setInfoblocks(currentComplect))
    dispatch(setFreeblocks(currentComplect))
    dispatch(setERs(currentComplect))
    dispatch(setPacketERs(currentComplect))
    dispatch(setLt(currentComplect, currentContract))
    dispatch(setConsalting(currentComplect, currentContract))

}

