import { createtUniversalDefaultComplect } from "../complect/current-complect-reducer"


export const RESET = 'RESET'
export const RESET_UNIVERSAL = 'RESET_UNIVERSAL'



export const reset = () => ({type: RESET})

const resetUniversalAC = () => ({type: RESET_UNIVERSAL})

export const resetUniversal = () => (dispatch, getState) => {

    dispatch(reset()) //for checkboxes
    dispatch(createtUniversalDefaultComplect()) //for create default current complect in complect reducer
}




