

const GlobalValues = ({ values, selects, message }) => {
   

    let valuesResult = values.map((value, index) => {
        // if(value){

        // }
        
        if (!value && message && message.includes(selects[index].name)) {
          
            return <p key={`global-value-${index}`} className='global__value--error '>{message}</p>
        } else if (value && value.title) {
            
            return <p key={`global-value-${index}`} className='global__value'>{value.title}</p>
        }
        else if (!value && !message) {
            return <p key={`global-value-${index}`} className='global__value'>{''}</p>
        }


    })
  
    return valuesResult

}

export default GlobalValues