import { connect } from "react-redux"
import App from "./App"
import { initial } from "./modules/redux/reducers/app/app-reducer"


const mapStateToProps = (state) => {
    return {
        preloader: state.preloader.inProgress,
        globalStatus: state.global.status,
        newDealId: state.deal.newDealId,

    }
}



export default connect(mapStateToProps, {
    initial
})(App)