import DeleteWhite from "../../../../../assets/img/DeleteWhite.svg";
import deleteBlack from "../../../../../assets/img/delete_black.svg";
import { reset } from "../../../../redux/reducers/reset/reset-reducer";
import Reset from './Reset';
import { connect } from "react-redux";

let mapStateToProps = (state) => {
  
    let deleteIcon = deleteBlack
    
    return {
        currentComplectsType:state.global.currentComplectsType,
        deleteIcon
    }

}

let ResetContainer = connect(mapStateToProps, {
    reset
})(Reset)
export default ResetContainer