import { PROF } from '../../../../redux/reducers/global/global-reducer'
import './Global.scss'

const Global = (props) => {

    return (
        <div className="global-header">
            <div className="global-header__info">
                {!props.isDealTable && <p className="global-header__title" id="complect__title">  {props.currentComplectsType.title}  </p>}
                {!props.isDealTable
                    ? <p className="global-header__supply"> " - {props.currentSupply.title}"</p>
                    : <p className="global-header__supply"> " - {props.currentComplect && props.currentComplect.name}"</p>
                }
                <p className="global-header__title">    {props.currentRegion.title}</p>
            </div>

            <div className="global-header__parameters">
                <p className="global-header__parameter global-header__parameter--reset"
                    onClick={props.currentComplectsType.title === PROF ? props.reset  : props.resetUniversal}
                >{ !props.isDealTable && ''}</p>
                <p className="global-header__parameter global-header__parameter--back"
                    onClick={props.isDealTable
                        ? () => props.setDealTableStatus(false)
                        : () => props.back()
                    }></p>
            </div>
        </div>
    )
}

export default Global
