let initialState = {

      add: {
        dealName: null,
        assigned: null,
        category: null,
        stage: null,
        opened: null,
    },
    product: {
        productId: null,
        price: null,
        measureId: null,
        quantity: null,
    },
    update: {
        contract: null,
        companyId: null,
        complectName: null,
        comments: null,
        freeBlocks: null,
        
        supply: null,
        supplyForContract: null,
    },
    fields: {}



}



//A

const SET_BITRIX_FIELDS = 'SET_BITRIX_FIELDS'


//AC

export const setBitrixFields = (add, product, update) => ({
    type: SET_BITRIX_FIELDS,
    add, product, update

})





//REDUCER




export const bitrixFields = (state = initialState, action) => {

    switch (action.type) {

        case SET_BITRIX_FIELDS:
           
            return {
                ...state,
                add: action.add,
                product: action.product,
                update: action.update,


            }
        default:
            return state
    }


}
































