import Button from '../../Common/Button/Button'
import Logo from '../../Common/Logo/Logo'
import GlobalValues from './Global-Values/GlobalValues'
import './SelectGlobal.scss'
import Selects from './Selects/Selects'


const SelectGlobal = (props) => {

    const selectsProps = [
        {
            name: props.supplies.name,
            values: props.supplies.values,
            selectCallback: props.setSupply
        },
        {
            name: props.complects.name,
            values: props.complects.values,
            selectCallback: props.setComplectsType
        },
        {
            name: props.regions.name,
            values: props.regions.values,
            selectCallback: props.setRegion
        },

    ]

    const currentValues = [
        props.currentSupply,
        props.currentComplectsType,
        props.currentRegion

    ]


    return (
        <div className='global__container'>

            <div className='global__main'>
                <div className='global__header'>
                    <div className='global__logo'>
                        <Logo />
                    </div>

                    <h1 className='glabal__appName'>  </h1>
                </div>
                <div className="global__menu">
                    <div className='global__selects'>
                        <Selects selects={selectsProps} />

                    </div>
                    <div className='global__result'>
                        <GlobalValues values={currentValues} selects={selectsProps} message={props.message} />
                    </div>
                    <div className='global__button'>
                        <div className='button__wrapper'>
                            <Button name={''} onButtonClick={() => { props.changeAndSetGlobalStatus(true) }} />
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}
export default SelectGlobal