// import "./prepaid.css"
import { TextField } from '@mui/material';


const DealName = (props) => {
    const onChange = (e) => {
        e.preventDefault()
        props.setDealName(e.target.value)
    }
    return (

        <TextField 
        
        name='title' 
        onChange={onChange} 
        fullWidth 
        id="push-deal-name" 
        label=" " 
        variant="outlined" 
        value={props.name} />

    )
}

export default DealName