import { LT_ERROR } from "../../../../../redux/reducers/checkboxes/legal-tech-reducer"
import { COMPLECT_ERROR } from "../../../../../redux/reducers/complect/current-complect-reducer"


const ResultItem= ({index, resultItem, errorItem, itemStyle, itemClass}) => {
   
 
    return ( 
        <div style={itemStyle} key={`result-item-${resultItem.type}-${index}`}
            className={itemClass}
        >
            <p key={resultItem.title}
                className={"result__title"}
            >{resultItem.title}</p>


            <p key={`${resultItem.title}-value-${index}`}
                className={resultItem.value === COMPLECT_ERROR || resultItem.value === LT_ERROR
                    ? 'result__value--error'
                    : 'result__value--selected'
                }>
                {resultItem.value}
            </p>
        </div>
    )
}

export default ResultItem
