import { connect } from "react-redux"
import { changeAndSetGlobalStatus, cleanMessage, setComplectsType, setRegion, setStatus, setSupply } from "../../../redux/reducers/global/global-reducer"
import SelectGlobal from "./SelectGlobal"


const mapState = (state) => {

    return {
        supplies: state.global.supplies,
        complects: state.global.complects,
        regions: state.global.regions,
        currentSupply: state.global.currentSupply,
        currentComplectsType: state.global.currentComplectsType,
        currentRegion: state.global.currentRegion,
        status: state.global.status,
        message: state.global.message
    }
}

const SelectGlobalContainer = connect(mapState, {
    setSupply,
    setComplectsType,
    setRegion,
    changeAndSetGlobalStatus,
    cleanMessage,
    
})(SelectGlobal)

export default SelectGlobalContainer