import { connect } from "react-redux";
import Checkboxes from "./Checkboxes";
import { INFOBLOCKS_GROUPS } from "../../../redux/reducers/checkboxes/infoblocks-reducer";
import { ER_GROUPS, PAKETS_ER_GROUPS } from "../../../redux/reducers/checkboxes/encyclopedias-reducer";
import { CONSALTING_GROUPS, FREE_GROUPS, LT_GROUPS } from "../../../redux/reducers/checkboxes/checkboxes-action";
import { changeER, changeFree, changeInfoblock, changeLt, changePacketER, changeConsalting } from "../../../redux/reducers/complect/current-complect-reducer";


let mapStateToProps = (state) => {

    return {
        allBlocks: [
            state.infoblocks,
            state.encyclopedias[0],
            state.encyclopedias[1],
            state.legalTech,
            state.consalting,
            state.freeBlocks
        ]

    }
}
const mapDispatchToProps = (dispatch) => {

    return {
        changeCheckbox: (type, value, checked, index) => {
            
            if (INFOBLOCKS_GROUPS.includes(type)) {

                dispatch(changeInfoblock(checked, value))

            } else if (PAKETS_ER_GROUPS.includes(type)) {
                dispatch(changePacketER(checked, index))   //THUNK in current-complect reducer

            } else if (ER_GROUPS.includes(type)) {
                dispatch(changeER(checked, index))   //THUNK in current-complect reducer

            } else if (LT_GROUPS.includes(type)) {

                dispatch(changeLt(checked, index))

            } else if (CONSALTING_GROUPS.includes(type)) {
                dispatch(changeConsalting(checked, index))

            } else if (FREE_GROUPS.includes(type)) {
                dispatch(changeFree(checked, index))
            }
        }
    }
}


const CheckboxesContainer = connect(mapStateToProps, mapDispatchToProps)(Checkboxes)

export default CheckboxesContainer
