import { LT_ERROR } from "../../../../../redux/reducers/checkboxes/legal-tech-reducer"
import { COMPLECT_ERROR } from "../../../../../redux/reducers/complect/current-complect-reducer"


const ComplectsName = ({index, resultItem, errorItem, complectNameStyle, complectNameClass}) => {
   

    return (
        <div style={complectNameStyle} key={`result-item-${resultItem.type}-${index}`}
            className={complectNameClass}
        >
            <p key={resultItem.title}
                className={"result__title"}
            >{resultItem.title}</p>


            <p key={`${resultItem.title}-value-${index}`}
                className={errorItem.value === COMPLECT_ERROR || resultItem.value === LT_ERROR
                    ? 'result__value--error'
                    : 'result__value--selected'
                }>
                {errorItem.value || resultItem.value}
            </p>
        </div>
    )
}

export default ComplectsName