const SelectItem= ({ name, values, selectCallback }) => {
  // const classes = useStyles();
  const [value, setValue] = React.useState('');

  const handleChange = (event) => {
    setValue(event.target.value);
  };


  const options = values.map((value, index) => (

    <MenuItem
      key={`item-od-${index}`}

      onClick={() => { selectCallback(value.id) }}>
      <Button
        key={`btn-od-${index}`}
        className="btn__od"
      // as="button"
      >{value.title}
      </Button>
    </MenuItem>

  ))



  return (

    <FormControl color="primary" className="formControlSelect">
      <InputLabel id="demo-simple-select-autowidth-label">
        {name}
      </InputLabel>
      <Select
        onChange={handleChange}
        // classes={{ root: classes.select }}
        style={{
          display: 'flex',
          maxWidthwidth: '90vw',
          alignItems: 'center',
          justifyContent: 'center',
          bgcolor: 'background.default',
          color: 'text.primary',
          borderRadius: 0,
          p: 3,
        }}
        className="global__select--ul">
        {options}
      </Select>
    </FormControl >

  );
}

export default SelectItem



