import './Deal-Field.css';
import FieldEr from './Field-Items/Er/Field-Er';
import FieldPacketsEr from './Field-Items/Er/Field-Packet-Er';
import FieldInfoblocks from './Field-Items/Field/Field-Infoblocks';
import FieldLt from './Field-Items/Lt/Field-Lt';









const DealField = ({ currentComplect, fields, encyclopedias, lt }) => {
  return (
    <>
      <div className="fields__infoblocks">
        {fields.map((fieldGroups, i) =>
          fieldGroups.map(
            (fieldGroup) =>
              fieldGroup.value.some((block) => block.checked) && (
                <FieldInfoblocks key={`deal-field-${fieldGroup.groupsName}-${i}`} group={fieldGroup} />
              )
          )
        )}
      </div>

      {(encyclopedias[0].value.some((packet) => packet.checked) ||
        encyclopedias[1].value.some(
          (erBlock) => erBlock.checked)) && <div className="fields__encyclopedias">
          {encyclopedias.map((groupEncyclopedias, i) => {
            if (
              groupEncyclopedias.groupsName ===
              "  " &&
              groupEncyclopedias.value.some((packet) => packet.checked)
            ) {
              return (
                <FieldPacketsEr
                  key={`deal-field-${groupEncyclopedias.groupsName}-${i}`}
                  groupEncyclopedias={groupEncyclopedias}
                  encyclopedias={encyclopedias}
                />
              );
            } else {
              return (
                groupEncyclopedias.value.some(
                  (erBlock) => erBlock.checked && erBlock.weight > 0
                ) && <FieldEr key={`deal-field-${groupEncyclopedias.groupsName}-${i}`} group={groupEncyclopedias} />
              );
            }
          })}
        </div>}

      {lt.value.some(l => l.checked) && <div className="fields__lt">
        <FieldLt currentComplect={currentComplect} lt={lt} />
      </div>}
    </>
  );
};

export default DealField;
































































