import React, { useEffect } from "react";
import "./Total-Table.css"
import OdContainer from "../Main/Complect/OD/Od-Container";
import { ContractSelectContainer } from "./Contract-Select/Contract-Select-Container";
import { DealNameContainer } from "./Deal-Name/Deal-Name-Container";
import { AddDealContainer } from "./Add-Deal/AddDeal-Container";
import { DealPriceTableContainer } from "./Deal-Price-Table/Deal-Price-Table-Container";
import GlobalContainer from "../Main/Konstructor/Global/Global-Container";
import DealFieldContainer from "./Deal-Field/Deal-Field-Container";


const TotalTable = (props) => {
  useEffect(() => {
    props.getField()
  }, [props.currentComplect])

  
  let dealResult = <>
    <div className="deal__container">
      <div className="deal__global">
        <GlobalContainer />
      </div>

      <div className="deal__main">
        <div className="deal-settings">

          <div className="deal-settings__item">
            <DealNameContainer />
          </div>
          <div className="deal-od deal-settings__item">
            <OdContainer />
          </div>
          <div className="deal-settings__item">
            <ContractSelectContainer />

          </div>


        </div>

        <div className="deal-fields">
          <DealFieldContainer />

        </div>
      </div>


      <div className="deal__footer">
        <div className="deal-price__container">
          <DealPriceTableContainer />
        </div>

        <div className="deal-buttons__container">
          <AddDealContainer />
        </div>
      </div>

    </div>
  </>

  return dealResult


};

export default TotalTable;

