import React from "react"
import CheckBox from "./Checkbox-Item/Checkbox"
import "./Checkboxes.scss"



const Checkboxes = (props) => {

    let groupsOfInfoblocks =
        props.allBlocks.map(group => {
            let items = []

            const checkBoxesCreator = (group) => {

                return group.value.map((element, index) => {

                    return <CheckBox
                        key={`${group.groupsName}-${index}`}
                        name={element.name}
                        title={element.title}
                        index={index}
                        groupsName={group.groupsName}
                        checked={element.checked}
                        changeCheckbox={props.changeCheckbox}
                    />
                }
                )


            }
            const groupCreator = (number) => {

                return (
                    <div key={`group-${group[number].groupsName}-${number}`} className="group">
                        <h2 key={`group-h2-${number}`} className="group__title">{group[number].groupsName}</h2>
                        {checkBoxesCreator(group[number])}
                    </div>
                )
            }


            if (group[0]) { //   -    
                for (let i = 0; i < group.length; i++) {

                    items[i] = groupCreator(i)
                }

            } else {

                items =
                    <div key={`group-${group.groupsName}`} className="group">
                        <h2 key={`group-h2-${group.groupsName}`} className="group__title">{group.groupsName}</h2>
                        {checkBoxesCreator(group)}
                    </div>

            }
            return items

        })

    return (
        <div id="menu" className="wrapper">
            {groupsOfInfoblocks}
        </div>
    )
}



export default Checkboxes
