import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@mui/material";

import "./Deal-Price-Table.scss";




const DealPriceTable = ({ currentComplect, price, lt, consalting, unit }) => {

    

    const createData = (type, name, prepaid, discount, total) => {



        let resultName = type === '' ? `${type} ${name}` : name

        return {

            'name': resultName,
            'prepaid': prepaid,
            'discount': `${discount} .`,
            'total': total,
            'price': total
        }


    }


    const products = () => {
        let rows = []
        if (currentComplect) {

            rows = [
                createData('', currentComplect.name, 0, 0, price)

            ]
            if (lt.current) {
                if (lt.current.name !== 'LT  ')
                    rows.push(createData(false, lt.current.title, 0, 0, lt.current.price))
            }
            if (consalting.current) {
                rows.push(createData(false, consalting.current.title, 0, 0, consalting.current.price))
            }
        }

        return rows
    }


    let rows = products()

    const getTotalSum = (rows, field) => {
        let price = rows.reduce((acc, row) => acc + row[field], 0);
        return Number(price.toFixed(2))
    };

    return (
        <TableContainer component={Paper} className={'table'}>
            <Table sx={{  minWidth: '100%' }} aria-label="simple table">
                <TableHead>
                    <TableRow sx={{ maxHeight: '10px !important' }}>
                        <TableCell></TableCell>
                        <TableCell className={'measure'} align="right">{unit}</TableCell>
                        <TableCell align="right">&nbsp;(.)</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.length > 0 && rows.map((row) => (
                        <TableRow
                            key={row.name}
                            sx={{  '&:last-child td, &:last-child th': { border: 60 }, maxHeight:'12px', }}
                        >
                            <TableCell component="th" scope="row" className="spanText">
                                {row.name}
                            </TableCell>
                            <TableCell align="right">1</TableCell>
                            <TableCell align="right">{row.total}</TableCell>
                        </TableRow>
                    ))}
                    {/*      */}
                    {rows.length > 1 && <TableRow>
                        <TableCell component="th" scope="row" className="spanText">
                            
                        </TableCell>
                        <TableCell align="right"></TableCell> {/*      */}
                        <TableCell align="right">
                            {getTotalSum(rows, 'total')}
                        </TableCell>
                    </TableRow>}
                </TableBody>
            </Table>
        </TableContainer>

    );
}

export default DealPriceTable