const FieldEr = ({ group }) => {
    return (
        <div className="er">
            <p className="field-group">{group.groupsName}: </p>
            {group.value.map(
                (er, i) => er.checked && er.weight !== 0 && <p  key={`deal-field-er-${group.groupsName}-${i}`} className="field-text"> {er.name} </p>
            )}
        </div>
    );
};


export default FieldEr