export const checkUncheckAndFieldsFree = (state, currentComplect) => {

    let stateCopy = { ...state }
    stateCopy.value = [...state.value]


    if (currentComplect) {
        let namesActiveFreeBlocks = []


        stateCopy.url = ''
        stateCopy.value = stateCopy.value.map((elem, index) => {

            let checked = true
            if (currentComplect.freeBlocks.includes(index)) {
                namesActiveFreeBlocks.push(elem.name)

                checked = true

            } else {
                namesActiveFreeBlocks = namesActiveFreeBlocks.filter(freeblockName => freeblockName !== elem.name)
                checked = false

            }


            return { ...elem, checked }

        })




        stateCopy.fields = namesActiveFreeBlocks
    } else {

        stateCopy.value.forEach((elem) => {

            elem.checked = false

        })

        stateCopy.fields = []

    }

    return stateCopy
}
