import { connect } from "react-redux"
import Result from "./Result"
import { getResult } from "../../../redux/reducers/result/result-reducer"

let mapStateToProps = (state) => {

       return {
        currentComplect: state.currentComplect,
        universalDefaultName : state.universals.universalDefaultComplect.name,
        od: state.od.currentOd,
        values: state.result.resultItems,
        price: state.price.current.price ,
        product: state.products.currentId ,
        contract: state.contract.current,
        styleLt: {
            display: state.legalTech.display
        },
        styleConsalting: {
            display: state.consalting.display
        },
     
    }
}

const ResultContainer = connect(mapStateToProps, {
    getResult
})(Result)
export default ResultContainer