

const FieldLt = ({ currentComplect, lt }) => {
    
    return (
        <>
            {currentComplect.ltInPacket.length > 0 && (
                <>
                    <p className="field-group"> Legal Tech - {lt.current && lt.current.name}: </p>
                    {lt.value.map(
                        (ltblock, index) =>
                            currentComplect.ltInPacket.includes(index) && (
                                <p  key={`deal-field-${ltblock.name}-${index}`} className="field-text">{ltblock.name}</p>
                            )
                    )}
                </>
            )}
            {currentComplect.lt.length > 0 && (
                <>
                    <p className="field-group">Legal Tech   : </p>
                    {lt.value.map((ltblock, index) =>
                        currentComplect.lt.includes(index) && (
                            <p key={`deal-field-${ltblock.name}-${index}`}  className="field-text">{ltblock.name}</p>
                        )
                    )}
                </>
            )}
        </>
    );
};

export default FieldLt