const SET_DEAL_NAME = 'SET_DEAL_NAME';

const initialState = { value: '' }
export const setDealName = (name) => ({ type: SET_DEAL_NAME, name })


export const dealName = (state = initialState, action) => {

    switch (action.type) {
        case SET_DEAL_NAME:

            return state.value !== action.name ? { ...state, value: action.name } : state

        default:
            return state
    }
}