    getDomain: () => {
        if (window.BX24.init) {
            return new Promise((resolve, reject) => {
                window.BX24.init(function () {
                    try {
                        const authData = window.BX24.getAuth();
                        if (authData) {
                          
                            resolve(authData.domain)
                        } else {
                            resolve(null)
                        }

                    } catch (error) {
                        reject(error);
                    }
                });
            });
        }

    }
























