import { calculatePrice, setUniversalPrices } from "../../../utils/reducers-utils/price/price-utils"
import { INTERNET, PROXIMA, KMV, STV, UNIVERSAL, PROF, SET_GLOBAL } from "../global/global-reducer"



const SET_PRICES = 'SET_PRICES'
const SET_CURRENT_PRICE = 'SET_CURRENT_PRICE'

const initialState = {

    prof: {
        regions: {
            internet: [],
            proxima: []
        },
        msk: {
            internet: [],
            proxima: []
        },
    },

    universal: {
        // kmv: [],
        // stv: []
    },

    current: {
        prices: [],
        price: 0
    }
}
//AC
export const setPrices = (prices) => ({ type: SET_PRICES, prices })

export const setCurrentPrice = (complectIndex, odIndex, typeOfContract, currentComplectsType) => (
    // like setCurrentGood without currentSupply, but with currentRegion
    {
        type: SET_CURRENT_PRICE,
        complectIndex,
        odIndex,
        typeOfContract,
        currentComplectsType,

    }
)

