const price = (state = initialState, action) => {

    switch (action.type) {
        case SET_PRICES:

            let coefficients = action.prices.coefficients

            coefficients.splice(0, 1)
            let universal = setUniversalPrices(coefficients, action.prices.universal)


            return {
                ...state,
                prof: { ...state.prof, ...action.prices.prof },
                universal

            }


        case SET_GLOBAL: //from global-parameters-reducer   = set current prices

            let currentPrices = []
            if (action.currentComplectsType && action.currentSupply && action.currentRegion) {
                if (action.currentComplectsType.title === UNIVERSAL) {

                    // if (action.currentRegion.title = KMV) {
                    //     if (action.currentSupply === INTERNET) { currentPrices = state.kmv.internet }
                    //     if (action.currentSupply === PROXIMA) { currentPrices = state.kmv.proxima }

                    // } else if (action.currentRegion = STV) {
                    if (action.currentSupply.title === INTERNET) { currentPrices = state.universal[action.currentRegion.name].internet }
                    if (action.currentSupply.title === PROXIMA) { currentPrices = state.universal[action.currentRegion.name].proxima }

                    // }



                } else if (action.currentComplectsType.title === PROF) {

                    if (action.currentRegion.name === 'msk') {
                        if (action.currentSupply.title === INTERNET) { currentPrices = state.prof.msk.internet }
                        if (action.currentSupply.title === PROXIMA) { currentPrices = state.prof.msk.proxima }
                    } else {
                        if (action.currentSupply.title === INTERNET) { currentPrices = state.prof.regions.internet }
                        if (action.currentSupply.title === PROXIMA) { currentPrices = state.prof.regions.proxima }
                    }


                }

                if (currentPrices.length > 0) {
                    return { ...state, current: { ...state.current, prices: currentPrices } };
                } else {

                    console.log('problem  with current price')
                    return state
                }
            } else {

                console.log('no action: current price')
                return state
            }


        case SET_CURRENT_PRICE:

            let currentPrice = calculatePrice(
                state.current.prices,
                action.complectIndex,
                action.odIndex,
                action.typeOfContract,
                action.currentComplectsType
            )

            return currentPrice !== state.current.price
                ? { ...state, current: { ...state.current, price: currentPrice } }
                : state
        default:
            return state
    }
}

export default price