import { LT_ERROR } from "../../../redux/reducers/checkboxes/legal-tech-reducer";

//UTILS
const updateState = (state, weightLt, ltIncluded, display, current) => {
    return {
        ...state,
        weightLt,
        ltIncluded,
        display,
        current
    };
};


export const checkUncheckLt = (legalTechs, currentComplect) => {
    return legalTechs.map((elem, index) => {
        const shouldCheck =
            currentComplect &&
            (currentComplect.lt.includes(index) ||
                currentComplect.ltInPacket.includes(index));

        return shouldCheck !== elem.checked ? { ...elem, checked: !elem.checked } : elem;
    });
};

export const weightLt = (stateCome, currentComplect) => {
    const value = stateCome.value.map((lt) => ({ ...lt }));
    const state = { ...stateCome, value };

    if (!currentComplect) return state;

    const { weightLt, ltIncluded } = state.value.reduce(
        (acc, element, index) => {
            if (currentComplect.ltInPacket.includes(index)) {
                if (element.checked) {
                    acc.weightLt += element.weight;
                }
            } else if (currentComplect.lt.includes(index)) {
                if (element.checked) acc.ltIncluded++;
            }
            return acc;
        },
        { weightLt: 0, ltIncluded: 0 }
    );

    const display = weightLt > 0 ? "flex" : "none";
    const ltPackage = state.packages[weightLt];

    if (ltPackage) {

        return updateState(
            state,
            weightLt,
            ltIncluded,
            display,
            ltPackage
        );
    } else if (weightLt === 0) {
        return updateState(state, weightLt, ltIncluded, display, null);
    } else {
        return updateState(
            state,
            weightLt,
            ltIncluded,
            display,
            { name: LT_ERROR, index: 0, weight: weightLt, description: '', productId: 0, productSix: 0, productEleven: 0, msk: 0, regions: 0, price: 0 }
        );
    }
};

const weightLtForResult = (stateCome, currentComplect) => {
    let value = stateCome.value.map(lt => {
        return {
            ...lt
        }
    })
    let state = {
        ...stateCome
    }
    state.value = value

    if (currentComplect) {
        state.ltIncluded = 0
        state.weightLt = 0

        state.value.forEach((element, index) => { //  LT
            if (currentComplect.ltInPacket.includes(index)) { //   LT         
                if (element.checked) {

                    state.weightLt = state.weightLt + element.weight
                }

            } else if (currentComplect.lt.includes(index)) {
                element.checked ? state.ltIncluded++ : state.ltIncluded = state.ltIncluded - 0

            }
            state.weightLt > 0 ? state.display = 'flex' : state.display = 'none'
            if (state.weightLt === 0) {
                state.nameOflt = ''
                state.priceOfLt = ''
                state.currentId = null
            }
            if (state.weightLt === 2) {
                state.nameOflt = ` `
                state.priceOfLt = state.pricesOfLt[0]
                state.currentId = state.ltIds[0]
            } else if (state.weightLt === 5) {
                state.nameOflt = `  `
                state.priceOfLt = state.pricesOfLt[1]
                state.currentId = state.ltIds[1]
            } else if (state.weightLt === 10) {
                state.nameOflt = `  `
                state.priceOfLt = state.pricesOfLt[2]
                state.currentId = state.ltIds[2]
            }
            if ((state.weightLt === 1 || state.weightLt > 2) && (state.weightLt < 5 || state.weightLt > 5) && (state.weightLt < 11)) {
                state.nameOflt = `LT  `
                state.currentId = null
            }
        })

    }

    return state
}