import "./Konstructor.scss"
import CheckboxesContainer from "../Checkboxes/Checkboxes-Container"
import ComplectSettings from "../Complect/Complect-Settings"
import ResultContainer from "../Result/ResultContainer"


const Konstructor = (props) => {
    
    return (
        <div className='konstructor__wrapper'>
            <div className={'main'}>
                <div className={'complect'}>
                    <ComplectSettings typeofComplect={props.typeofComplect.title} />
                </div>
                <div className={'infoblocks'}>
                    <CheckboxesContainer />
                </div>
                {props.currentComplect && !props.modal &&  <ResultContainer/>}
            </div>
        </div>
    )
}


export default Konstructor