import * as React from 'react';
import './Od.css'
import Selects from '../../../Common/Selects/Selects';

const Od = (props) => {
  let ods = props.ods.map((od, index) => ({ id: index, title: od }))
  return <Selects
    name={' '}
    items={props.ods}
    currentValue={props.currentOd}
    onSelectClick={props.odChange}
  />
}

export default Od