//A
const SET_ER = 'SET_ER'
const SET_PACKET_ER = 'SET_PACKET_ER'


//AC
export const setERs = (currentComplect) =>  ( { type: SET_ER, currentComplect })
export const setPacketERs = (currentComplect) => ({ type: SET_PACKET_ER, currentComplect })


//REDUCER
export const encyclopedias = (state = initialState, action) => {

  switch (action.type) {

    case SET_ER:
      return  erCheckUncheck(state, action.currentComplect)

    case SET_PACKET_ER:
      return erCheckUncheck(state, action.currentComplect)

    case RESET || RESET_UNIVERSAL:
      return uncheckBlocks(state)
    default:
      return state
  }

}

export default encyclopedias