import "./Result-Button.scss"
import "../../Complect/Complects/Complects.scss"
// import '../../../../styles/components_colors/btns_color.css'
import * as React from 'react';
import { Button, Dialog, Slide, List } from "@mui/material"
import { UNIVERSAL } from "../../../../redux/reducers/global/global-reducer";
import { useState } from "react";
import TotalTableContainer from "../../../Total-Table/Total-Table-Container";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ResultButton = (props) => {

  // const [show, setShow] = useState(false)


  let ref = React.createRef()

  const handleClickOpen = () => {
    // setShow(true)
    props.handleShow(ref)
  };

  const handleClose = () => {
    // setShow(false)
    props.handleClose(ref)
  };


  // let buttonStyle = props.styleBtnModal
  let buttonClassname = 'btn-result btn'
  let disabled = false
  let title = ' '

  if (props.currentComplectsType === UNIVERSAL) {
    // buttonStyle = null
    buttonClassname = 'btn-result-universal--active'
    if (props.resultError || props.currentComplect.name === ' ') {
      buttonClassname = 'btn-result-universal'
      disabled = true
      title = props.resultError
    }

  } else {

    if (props.currentComplect && props.currentComplect.name !== ' ') {
      buttonClassname = `btn-result btn ${props.currentComplect.className}--active`
    }
  }

  return (
    <div className="btn-result_container">
      <Button ref={ref} disabled={disabled} onClick={handleClickOpen} style={{ border: 'none' }} className={buttonClassname}>
        {title}

      </Button>
      <Dialog
        fullScreen
        open={props.isDealTable}
        onClose={handleClose}
        TransitionComponent={Transition}
      >

        <List>
          <TotalTableContainer />

        </List>
      </Dialog>
    </div>
  );
}


export default ResultButton

