import React, { useEffect } from "react"
import "./Result.scss"
import ResultItems from "./Result-Items/Result-Items "
import ResultButtonContainer from "./Result-Button/Result-Button-Container"


const Result = ({currentComplect,universalDefaultName, od, price, product, contract, values, styleLt, styleConsalting, getResult}) => {

    useEffect(() => {    
        
        getResult()
    }, [currentComplect, od, price, product, contract])


    if (currentComplect.name && currentComplect.name !== universalDefaultName) {
        return (
            <div id="" className="result__container">
                <div className="information">
                    <ResultItems 
                    values={values} 
                    styleLt={styleLt}
                    styleConsalting={styleConsalting}
                    />

                </div>
                <ResultButtonContainer />

            </div>
        )
    } else {
        return null

    }


}

export default Result