


let initialState = {
    infoblocks: '',
    infoblocksDescription: [],
    er: '',
    lt: [],
    freeBlocks: [],
    consalting: [],
    current: '',
    description: [],

}



//A
const SET_DEAL_FIELD = 'SET_DEAL_FIELD'



//AC
const setField = (currentComplect, infoblocks = 0, er = 0, lt = 0, freeBlocks = 0, consalting = 0) => {


    return {
        type: SET_DEAL_FIELD,
        infoblocks,
        er,
        lt,
        freeBlocks,
        consalting,
        currentComplect
    }
}

//THUNK

export const getField = () => (dispatch, getState) => {
    const state = getState()

    const currentComplect = state.currentComplect
    const infoblocks = state.infoblocks
    const er = state.encyclopedias
    const lt = state.legalTech
    const freeBlocks = state.freeBlocks
    const consalting = state.consalting

    dispatch(setField(currentComplect, infoblocks, er, lt, freeBlocks, consalting))

}

// UTILS
let infoblocks = (array) => {
    let result = []


    array.forEach((elem, idx) => {
        let resultArray = ``
        let type = array[idx].groupsName
        elem.value.forEach((element) => {

            if (element.checked) {
                resultArray += `${element.title || element.name}
                `
            }
        })
        if (resultArray) {
            result.push(resultArray)
        }
    })
    let stringResult = ``
    result.forEach(element => {
        stringResult += `${element} `
    })

    return (
        stringResult
    )
}


let setFreeBlocksFields = (array) => {

    return array.value.map((element) => {
        return element.checked ? element.name : null
    })

}

const enciclopedias = (currentComplect, arrayOfPakets, arrayOfEr) => {

    let result = ``
    let indexesOfIncludedErs = []
    let paketsEr = (currentComplect, arrayOfPakets, arrayOfEr) => {
        let paketsNames = ``
        let includedOfPakets = ``
        let paketsAndFilling = ``
        if (currentComplect) {
            if (currentComplect.packetsEr) {
                // if(currentComplect.fillingPaketsERIndexes.length < 2){ //        - ..  
                currentComplect.packetsEr.forEach(paketIndex => {
                    paketsNames += `${arrayOfPakets.value[paketIndex].name}
                     `


                    arrayOfPakets.value[paketIndex].including.forEach(indexOfEr => {
                        //                         if(!includedOfPakets.includes(indexOfEr)){

                        //                             includedOfPakets += `
                        // ${arrayOfEr.value[indexOfEr].name}</br>`
                        //                         }
                        if (!indexesOfIncludedErs.includes(indexOfEr)) {

                            indexesOfIncludedErs.push(indexOfEr)
                        }
                    })
                })
                indexesOfIncludedErs.forEach(indexOfEr => {
                    includedOfPakets += `${arrayOfEr.value[indexOfEr].name} 
                    `
                })

                paketsAndFilling = `${paketsNames} 
                `
                paketsAndFilling += `${includedOfPakets} 
                `
            }
        }

        return paketsAndFilling
    }

    let er = (currentComplect, arrayOfEr, indexesOfIncludedErs) => {

        let result = ``

        if (currentComplect.ers.length > 0) {
            result = `${arrayOfEr.groupsName}
            `

            arrayOfEr.value.forEach((element, index) => {
                if (element.checked === true) {

                    if (!indexesOfIncludedErs.includes(index)) {
                        result += `${element.name}
                        `
                    }

                }
            })


        }

        return result
    }
    let paketsString = paketsEr(currentComplect, arrayOfPakets, arrayOfEr)
    let erString = er(currentComplect, arrayOfEr, indexesOfIncludedErs)


    result += paketsString
    result += erString
    // result += `/<pre>`
    return result
}

let setLtFields = (currentComplect, legalTech) => {

    let resultLts = []

    if (legalTech.current) {



        // resultLts.push(`${legalTech.current.title} :`)

        legalTech.value.forEach((lt, i) => {
            lt.checked && currentComplect.ltInPacket.includes(i) &&
                resultLts.push(lt.name)

        })


    }
    if (currentComplect.lt.length > 0) {
        legalTech.value.forEach((lt, i) => {
            lt.checked && currentComplect.lt.includes(i) &&
                resultLts.push(lt.name)

        })
    }
    
    return resultLts
}
let setConsaltingFields = (currentComplect, consalting) => {

    let resultLts = []

    if (currentComplect.consalting.length > 0 || currentComplect.consaltingProduct.length > 0) {

        consalting.value.forEach((c, i) => {
            if (c.checked) {

                if (currentComplect.consalting.includes(i) ||
                    currentComplect.consaltingProduct.includes(i)) {
                    resultLts.push(c.name)
                }
            }
        })
    }

    return resultLts
}



//REDUCER


const changeField = (stateCome, action) => {

    if (action.currentComplect) {
        let infoblocksString = ''
        let erString = ''
        let ltArray = []
        let freeBlocks = ''
        let consalting = []

        let state = {
            ...stateCome
        }

        infoblocksString = infoblocks(action.infoblocks)
        erString = enciclopedias(action.currentComplect, action.er[0], action.er[1])
        ltArray = setLtFields(action.currentComplect, action.lt)
        freeBlocks = setFreeBlocksFields(action.freeBlocks)
        consalting = setConsaltingFields(action.currentComplect, action.consalting)




        state.infoblocks = infoblocksString
        state.er = erString
        state.lt = ltArray
        state.freeBlocks = freeBlocks
        state.consalting = consalting
    

        state.current = `${infoblocksString}
${erString}
`
        console.log(state.current)
        debugger
        return state
    } else {
        return stateCome
    }

}

export const field = (state = initialState, action) => {

    switch (action.type) {

        case SET_DEAL_FIELD:
            return changeField(state, action);


        default:
            return state
    }


}
















































