//A
const SET_FREE_BLOCKS = 'CHANGE_FREE_BLOCKS';

//AC
export const setFreeblocks = (currentComplect) => ({ type: SET_FREE_BLOCKS, currentComplect })





//REDUCER
const freeBlocks = (state = initialState, action) => {

    switch (action.type) {


        case SET_FREE_BLOCKS:

            return checkUncheckAndFieldsFree (state, action.currentComplect)

        case RESET || RESET_UNIVERSAL:


            return uncheckBlocks([state])[0]

        default:
            return state
    }

}

export default freeBlocks