import React from 'react';
import "./Complects.scss"
import Button from '@mui/material/Button';
import { PROF } from "../../../../redux/reducers/global/global-reducer";



const Complects = (props) => {

    let buttons = [];
let currentSupplyName

    if (props.currentComplectsType === PROF) {
        buttons = props.profs.map((complect, index) => (


            <Button
                key={`btn-${index}`}
                style={{
                    lineHeight: '1px',
                    
                }}
                onClick={() => {
                    props.createComplect(
                        complect,
                        index)
                }}

                className={`${props.currentComplect && props.currentComplect.name === complect.name ? `${complect.className}--active` : complect.className} complect__grid-item`}
                number={index}
                type="button" > {complect.title || complect.name}
                {/* < div className="ellipseWrapper" >
                    <img className="ellipse" src={props.currentComplect && complect.name === props.currentComplect.name ? EllipseCheck : Ellipse} alt=""></img>
                </div > */}
            </Button >)
        )
    } else {
        if (props.currentComplect) {

            buttons = props.universals.map((complect, index) => {

                return (
                    <Button
                        key={`btn-${index}`}
                        className={`${props.currentComplect && props.currentComplect.name === complect.name ? `${complect.className}--active` : complect.className} complect__grid-item`}
                        number={index}
                        onClick={() => {
                            if (complect.name === '') {
                                props.createComplect(
                                    complect,
                                    index)
                            }
                        }}
                        type="button" >{complect.name}

                        {/* <div className="ellipseWrapper">
                            {(index === props.universals.length - 1 || props.currentComplect.name === complect.name) && <img className="ellipse" src={complect.name === props.currentComplect.name ? EllipseCheck : Ellipse} alt=""></img>}
                        </div> */}
                    </Button>
                )
            })
        }
    }

    return (
        buttons
    )
}

export default Complects

