export const setUniversalPrices = (coefficients, universal) => {

    let result = {}
    for (const key in universal) {
        if (universal.hasOwnProperty(key)) {

            let internet = coefficients.map(coef => {
                return universal[key].map(localComplectPrice =>
                    Number((coef * localComplectPrice).toFixed(2)))
            })

            let proxima = [
                universal[key].
                    map(universalComplectProxima => Number((universalComplectProxima * coefficients[0]).toFixed(2))),
                ...internet
            ]



            result[key] = {
                internet,
                proxima
            }
        }

    }

    return result
}



