import { bitrixAPI } from "../../../services/bitrix-general-api";
import { setPreloader } from "../preloader/preloader-reducer";
import { RESET, RESET_UNIVERSAL } from "../reset/reset-reducer";


const initialState = {
    newDealId: null,
    table: {
        isActive: false
    }
}

//A
const SET_NEW_DEAL = 'SET_NEW_DEAL'
const DELETE_CREATED_DEAL = 'DELETE_CREATED_DEAL'
const SET_DEALTABLE_STATUS = 'SET_DEALTABLE_STATUS'


//AC
const setNewDeal = (deal) => ({ type: SET_NEW_DEAL, deal })
const deleteCreatedDeal = () => ({ type: DELETE_CREATED_DEAL })

export const setDealTableStatus = (bool) => ({ type: SET_DEALTABLE_STATUS, bool })

//THUNK
export const sendDeal = () => async (dispatch, getState) => {

    dispatch(setPreloader(true))

    const state = getState()
    const dealName = state.dealName.value
    const currentUserId = state.app.user
    const domain = state.app.domain
    const bitrixAdd = state.bitrix.add
    const newDealId = await bitrixAPI.addDeal(dealName, currentUserId, bitrixAdd)



    const currentComplectName = state.currentComplect && state.currentComplect.name

    //PRODUCT
    const productId = state.products.currentId
    const price = state.price.current.price

    //SERVICES
    const lt = state.legalTech.current
    const consalting = state.consalting.current


    let products = [{ id: productId, price: price }]
    lt && products.push({ id: lt.currentProduct, price: lt.price })
    consalting && products.push({ id: consalting.currentProduct, price: consalting.price })


    const measureId = state.contract.current.measure.id
    const bitrixProduct = state.bitrix.product
    await bitrixAPI.setProducts(newDealId, products, measureId, bitrixProduct)


    const companyId = state.app.company
    const od = state.od.currentOd
    const contractId = state.contract.current.id
    const dealField = state.field.current
    const freeBlocksFields = state.field.freeBlocks
    const ltFields = state.field.lt
    const consaltingFields = state.field.consalting
    const bitrixUpdate = state.bitrix.update

    console.log('consaltingFields')
    console.log(consaltingFields)

    // const updatingDeal = 
    await bitrixAPI.updateDeal(newDealId, contractId, companyId, currentComplectName, od, dealField, freeBlocksFields, ltFields, consaltingFields, bitrixUpdate)


    dispatch(setNewDeal(newDealId))//todo reset or redirect to redirect
    dispatch(setPreloader(false))
    window.top.location.replace(`https://${domain}/crm/deal/details/${newDealId}/`)

    // document.location.reload();


}


const deal = (state = initialState, action) => {

    switch (action.type) {
        case SET_NEW_DEAL: return { ...state, newDealId: action.deal };
        case DELETE_CREATED_DEAL: return { ...state, newDeal: null };
        case SET_DEALTABLE_STATUS: return state.table.isActive !== action.bool
            ? { ...state, table: { ...state.table, isActive: action.bool } }
            : state

        case RESET_UNIVERSAL || RESET: return {
            ...state, newDealId: null,
            table: { ...state.table, isActive: false }
        }
        default:
            return state;
    }
}

export default deal