import Gradient from "../Common/Backgrounds/Gradient"
import './Finish.scss'
import Logo from '../Common/Logo/Logo'



const Finish = ({ deal, domain }) => {

    if (deal && domain) {
        return (
            <Gradient isActive={false}>
                <Logo />
            </Gradient>
        )
    } else {
        document.top.location.reload();
    }

}

export default Finish