
import { connect } from "react-redux";
import Od from "./Od";
import { odChange } from "../../../../redux/reducers/od/od-reducer";
import { changeCurrentProductAndPrice } from "../../../../redux/reducers/deal/products-reducer";


let mapStateToProps = (state) => {
  let currentComplect = state.currentComplect

  let numberOfComplect = 0
  if (currentComplect) {
    numberOfComplect = currentComplect.number
  }

  return {
    currentComplect,
    numberOfComplect,
    ods: state.od.currentNames,
    currentOd: state.od.currentOd,
  }
}

let mapDispatchToProps = (dispatch) => {

  return {
    odChange: (index) => {
      dispatch(odChange(index))
      dispatch(changeCurrentProductAndPrice())
    }
  }
}


const OdContainer = connect(mapStateToProps, mapDispatchToProps)(Od)
export default OdContainer