import Button from '@mui/material/Button';
import { PROF } from '../../../../redux/reducers/global/global-reducer';

import "./Reset.scss"


const Reset = (props) => {

    let imgStyle = {
        display: 'flex'
    }
    if (props.currentComplectsType === PROF) {
        return (
            <Button
                
                onClick={props.reset}
                className="btn__reset complect__grid-item"
                type="button" >
                <div></div>
                <div className="ellipseWrapper">
                    <img src={props.deleteIcon} style={imgStyle} alt="delete icon" />
                </div>
            </Button>
        )
    }else{
        return null
    }

}

export default Reset