export const erCheckUncheck = (state, currentComplect) => {

    let packets = state[0].value.map((packet, index) => {
      if (currentComplect.packetsEr.includes(index)) {
        packet.checked = true
        packet.weight = 1
      } else {
        packet.checked = false
        packet.weight = 0
      }
      return { ...packet }
  
    })
  
    let ers = state[1].value.map((er, index) => {
      if (currentComplect.ers.includes(index)) {
        er.checked = true
        er.weight = 0.5
      } else if (currentComplect.ersInPacket.includes(index)) {
        er.checked = true
        er.weight = 0
      } else {
        er.checked = false
        er.weight = 0
      }
      return { ...er }
  
    })
  
    return [{ ...state[0], value: packets }, { ...state[1], value: ers }]
  }