
import { fillingConsalting, fillingEr, fillingFreeblocksWithUncheck, fillingInfoblocks, fillingLt, fillingPketsEr, newFreeblocksWithUncheck } from "../../../utils/reducers-utils/complect/complect-utils"
import { setCheckboxes } from "../checkboxes/checkboxes-action"
import { setConsalting } from "../checkboxes/consalting-reducer"
import { setERs, setPacketERs } from "../checkboxes/encyclopedias-reducer"
import { setFreeblocks } from "../checkboxes/freeblocks-reducer"
import { setInfoblocks } from "../checkboxes/infoblocks-reducer"
import { setLt } from "../checkboxes/legal-tech-reducer"
import { changeCurrentProductAndPrice } from "../deal/products-reducer"
import { UNIVERSAL } from "../global/global-reducer"
import { RESET } from "../reset/reset-reducer"





let initialState = null



//CONST
export const COMPLECT_ERROR = '  '

//A
const CREATE_COMPLECT = 'CREATE_COMPLECT'
const SET_WEIGHT_AND_NAME_OF_UNIVERSAL_COMPLECT = 'CHANGE_WEIGHT_AND_NAME_OF_UNIVERSAL_COMPLECT'
const CHANGE_PACKET_ER = 'CHANGE_PACKET_ER'
const CHANGE_ER = 'CHANGE_ER'
const CHANGE_FREE = 'CHANGE_FREE'
const CHANGE_INFOBLOCK = 'CHANGE_INFOBLOCK'
const SET_FILLING_LT = 'SET_FILLING_LT'
const SET_FILLING_CONSALTING = 'SET_FILLING_CONSALTING'




/////////////////////////ACTION CREATORS

export const resetActionCreator = () => ({ type: RESET })

export const setUniversalComplect = (weight, currentComplectsType, universalComplects) =>
    ({ type: SET_WEIGHT_AND_NAME_OF_UNIVERSAL_COMPLECT, weight, currentComplectsType, universalComplects })


export const setCurrentComplect = (defaultComplect, index, ods, currentOd) => ({ // ==maximum
    type: CREATE_COMPLECT, defaultComplect, index, ods, currentOd
})

const setFillingER = (packets, checked, index) => ({ type: CHANGE_ER, packets, checked, index })
const setFillingPacketER = (packets, checked, index) => ({ type: CHANGE_PACKET_ER, packets, checked, index })
const setFillingInfoblock = (checked, value) => ({ type: CHANGE_INFOBLOCK, checked, value })
const setFillingFree = (checked, index) => ({ type: CHANGE_FREE, checked, index })
const setFillingLt = (checked, index) => ({ type: SET_FILLING_LT, checked, index })
const setFillingConsalting = (checked, index) => ({ type: SET_FILLING_CONSALTING, checked, index })

// export const setError = (error, complectsType) => ({ type: SET_ERROR, error, complectsType })  //for invalid weight




//THUNK
export const createComplect = (defaultComplect, index) => (dispatch, getState) => {

    const od = getState().od
    dispatch(setCurrentComplect(defaultComplect, index, od.names, od.currentOd))
    const currentComplect = getState().currentComplect
    dispatch(setCheckboxes(currentComplect))
    // dispatch(setConsalting(currentComplect))
}

export const createtUniversalDefaultComplect = () => (dispatch, getState) => {

    let state = getState()
    let universalDefaultComplect = { ...state.universals.universalDefaultComplect }
    let ods = state.od.names
    let currentOd = state.od.currentOd
    dispatch(setCurrentComplect(universalDefaultComplect, 8, ods, currentOd))
    const currentComplect = getState().currentComplect
    dispatch(setCheckboxes(currentComplect))

}

export const changeUniversalComplect = (weight, currentComplectsType, universalComplects) => (dispatch, getState) => {

    dispatch(setUniversalComplect(weight, currentComplectsType, universalComplects))
    const od = getState().od
    const currentComplect = getState().currentComplect
    dispatch(setCurrentComplect(currentComplect, currentComplect.number, od.names, od.currentOd))
    dispatch(changeCurrentProductAndPrice())

}



export const changeInfoblock = (checked, value) => (dispatch, getState) => {

    dispatch(setFillingInfoblock(checked, value))
    let currentComplect = getState().currentComplect
    dispatch(setInfoblocks(currentComplect))
    dispatch(setFreeblocks(currentComplect))
}

export const changeFree = (checked, index) => (dispatch, getState) => {
    dispatch(setFillingFree(checked, index))  //   current complect
    let currentComplect = getState().currentComplect  //  current complect
    dispatch(setFreeblocks(currentComplect)) //   freeblock-reducer,    checked    current complect
}


export const changeER = (checked, index) => (dispatch, getState) => {
    const packets = getState().encyclopedias[0].value
    dispatch(setFillingER(packets, checked, index))
    let currentComplect = getState().currentComplect

    dispatch(setERs(currentComplect))
}

export const changePacketER = (checked, index) => (dispatch, getState) => {
    const packets = getState().encyclopedias[0].value
    dispatch(setFillingPacketER(packets, checked, index))
    let currentComplect = getState().currentComplect
    dispatch(setPacketERs(currentComplect))
}

export const changeLt = (checked, index) => (dispatch, getState) => {
    dispatch(setFillingLt(checked, index))  //   current complect
    let currentComplect = getState().currentComplect  //  current complect
    let contract = getState().contract.current
    dispatch(setLt(currentComplect, contract)) //   legal-tech-reducer,    checked    current complect
}


export const changeConsalting = (checked, index) => (dispatch, getState) => {
    dispatch(setFillingConsalting(checked, index))  //   current complect

    let currentComplect = getState().currentComplect  //  current complect
    let contract = getState().contract.current
    debugger
    dispatch(setConsalting(currentComplect, contract)) //   legal-tech-reducer,    checked    current complect
    dispatch(setFreeblocks(currentComplect)) //   freeblock-reducer,    checked    current complect
}






///////////////////REDUCER
export const currentComplect = (state = initialState, action) => {
    let currentComplect = state
    switch (action.type) {
        case CREATE_COMPLECT:
            // return createComplect(state, action)

            if (action.defaultComplect) {
                state = { ...action.defaultComplect }

            }
            return state


        case SET_WEIGHT_AND_NAME_OF_UNIVERSAL_COMPLECT:
            if (state) {

                currentComplect = { ...state }
                if (action.currentComplectsType.title === UNIVERSAL) {
                    let indexOfRightComplect = 0

                    action.universalComplects.forEach(complect => {

                        if (action.weight === complect.weight) {
                            indexOfRightComplect++
                            currentComplect.number = complect.number
                            currentComplect.name = complect.name
                            currentComplect.weight = complect.weight
                        }
                    })
                    if (!indexOfRightComplect && action.weight) {
                        currentComplect.name = COMPLECT_ERROR
                    }
                    return currentComplect
                }
                return state
            }
            return state

        case CHANGE_INFOBLOCK:
            return fillingInfoblocks(state, action.checked, action.value)


        case CHANGE_FREE:
            //action: checked, index, value

            if (state) { //current complect

                currentComplect = { ...state }
                if (currentComplect.isChanging) {
                    if (action.checked) {  //      current
                        if (currentComplect.freeBlocks.includes(action.index)) {//     current

                            currentComplect.freeBlocks = fillingFreeblocksWithUncheck(currentComplect, action.index)
                        }
                    } else { //   
                        if (!currentComplect.freeBlocks.includes(action.index)) {//      current
                            if (action.index === 10 || action.index === 11 || action.index === 12) {
                               
                            } else {
                                currentComplect.freeBlocks.push(action.index)
                            }


                        }
                    }
                }
                return currentComplect
            }

            return state



        case CHANGE_ER:
            // action:  checked, index
            if (currentComplect) {
                let resultCurrentComplect = { ...currentComplect }
                if (currentComplect.isChanging) {
                    resultCurrentComplect = fillingEr(currentComplect, action.packets, action.checked, action.index)
                }
                return resultCurrentComplect


            } else {
                alert('  !')
                return state
            }

        case CHANGE_PACKET_ER:
            return fillingPketsEr(state, action.packets, action.checked, action.index)

        case SET_FILLING_LT:
            return fillingLt(state, action.checked, action.index)

        case SET_FILLING_CONSALTING:
            return fillingConsalting(state, action.checked, action.index)

        case RESET:
            return null


        default:
            return state
    }


}