//A
export const SET_INFOBLOCK = 'SET_INFOBLOCK'
export const SET_FETCHED_REGION_INFOBLOCKS = 'SET_FETCHED_REGION_INFOBLOCKS'

//AC
export const setInfoblocks = (currentComplect) => ({
    type: SET_INFOBLOCK, currentComplect
})






//REDUCER
const infoblocks = (state = initialState, action) => {
    let result = state
    switch (action.type) {

        // case CHANGE_CHECKBOXES_FROM_NEW_COMPLECT: //   current complect
        //     if (action.currentComplect) {

        //         result = checkUncheck(state, action.currentComplect)

        //     } else {
        //         result = uncheckBlocks(state)
        //     }
        //     return result

        case SET_INFOBLOCK:                    //   checkbox-infoblock
            if (action.currentComplect) {

                result = checkUncheck(state, action.currentComplect)

            } else {
                alert('  !')
            }
            return result

        case SET_GLOBAL:
            //action: title
            
            if (state[0].value[1].name !== action.currentRegion.infoblock) {
                const result = [...state]
                result[0] = { ...state[0] }
                result[0].value = [...state[0].value]
                result[0].value[1] = { ...state[0].value[1] }
                result[0].value[1].title = action.currentRegion.infoblock

                return result
            }
            return state


        case RESET || RESET_UNIVERSAL:
            return uncheckBlocks(state)

        default:
            return state
    }
}

export default infoblocks