const FieldPacketsEr = ({ groupEncyclopedias, encyclopedias }) => {
    const activePackets = groupEncyclopedias.value.filter(
        (packet) => packet.checked
    );
    return (
        <div className="packets">
            {activePackets.map((packet, packetIndex) => {
                if (activePackets.length === 1) {
                    return (
                        <p key={`deal-field-${packet.name}-${packetIndex}`} className="field-group">{packet.name}: </p>
                    );
                } else {
                    return packetIndex < activePackets.length - 1 ? (
                        <p key={`deal-field-${packet.name}-${packetIndex}`} className="field-group">{packet.name}, </p>
                    ) : (
                        <p key={`deal-field-${packet.name}-${packetIndex}`} className="field-group">{packet.name}: </p>
                    );
                }
            })}
            {encyclopedias[1].value.map(
                (er) => er.checked && er.weight === 0 && <p key={`deal-field-er-${er.name}`} className="field-text"> {er.name} </p>
            )}
        </div>
    );
};

export default FieldPacketsEr