import { INTERNET, PROXIMA, UNIVERSAL, PROF, SET_GLOBAL } from "../global/global-reducer";

const CONTRACT_INTERNET = 'Internet'
const CONTRACT_PROXIMA = ' '
export const CONTRACT_SIX = ' '
export const CONTRACT_ELEVEN = ' '


const initialState = {
    universal: {
        internet: [
            // {
            //     id: 0,
            //     value: 'Internet',
            //     measure: {
            //         id: 0,
            //         value: ''
            //     },
            //     measureIndex: 0

            // }
        ],
        proxima: []
    },
    prof: {
        internet: [],
        proxima: []
    },

    select: [],
    current: null


}

//A
const SET_CURRENT_CONTRACT = 'SET_CURRENT_CONTRACT'
const SET_FETCHED_CONTRACTS = 'SET_FETCHED_CONTRACTS'
export const SET_CONTRACT_IN_BLOCKS = 'SET_CONTRACT_IN_BLOCKS'


//AC

const setCurrentContract = (index) => ({ type: SET_CURRENT_CONTRACT, index })
export const setFetchedContracts = (contract, measure) => ({ type: SET_FETCHED_CONTRACTS, contract, measure })
export const setContractInBlocks = (currentContract) => ({ type: SET_CONTRACT_IN_BLOCKS, currentContract })


//THUNK
export const changeContract = (index) => (dispatch, getState) => {
    
    dispatch(setCurrentContract(index))
    let currentContract = getState().contract.current
    dispatch(setContractInBlocks(currentContract))

}



//REDUCER
export const contract = (state = initialState, action) => {

    switch (action.type) {

        case SET_FETCHED_CONTRACTS:
            let measures = action.measure.items

            const contracts = action.contract.items


            const resultContract = {
                ...state
            }

            contracts.forEach(contract => {
                if (contract.value === CONTRACT_INTERNET) {
                    resultContract.universal.internet.push({ ...contract, measure: measures[contract.measureIndex] })
                    resultContract.prof.internet.push({ ...contract, measure: measures[contract.measureIndex] })

                }
                if (contract.value === CONTRACT_SIX || contract.value === CONTRACT_ELEVEN) {

                    resultContract.prof.internet.push({ ...contract, measure: measures[contract.measureIndex] })
                }
                if (contract.value === CONTRACT_PROXIMA) {
                    resultContract.universal.proxima.push({ ...contract, measure: measures[contract.measureIndex] })
                    resultContract.prof.proxima.push({ ...contract, measure: measures[contract.measureIndex] })
                }
            })

            return resultContract

        case SET_GLOBAL:
            //action from global-reducer
            //currentSupply, currentComplectsType, currentRegion
            if (action.currentComplectsType && action.currentSupply) {
                let select
                let current

                if (action.currentComplectsType.title === UNIVERSAL) {
                    if (action.currentSupply.title === INTERNET) { select = state.universal.internet }
                    if (action.currentSupply.title === PROXIMA) { select = state.universal.proxima }

                } else if (action.currentComplectsType.title === PROF) {
                    if (action.currentSupply.title === INTERNET) { select = state.prof.internet }
                    if (action.currentSupply.title === PROXIMA) { select = state.prof.proxima }

                }
                select ? current = select[0] : current = current

                return { ...state, select, current }
            } else {
                return state
            }


        case SET_CURRENT_CONTRACT:

            return state.current && state.current.value !== state.select[action.index].value ? { ...state, current: state.select[action.index] } : state


        default:
            return state;
    }
}